@import '../../css/variables';

#contact {
    padding: 5rem 0;
    margin: 5rem 0;
    @include centering-flex(center, row);
    .container {
        width: 70%;
        @include centering-flex(center,column );
        h2 {
            color: $primary-color;
            font-size: 2.6rem;
            font-family: $secondary-font;
            margin-bottom: 2rem;
        }

        form {
            @include centering-flex(center,column );
            width: 60%;

            input[type=text], input[type=email], select{
                width: 100%;
                padding: 1.2rem 2rem;
                margin: 1rem 0;
                outline: none;    
                border: 2px solid $primary-color;  
                border-radius: 0.5rem;
            }

            textarea {
                width: 100%;
                padding: 1rem 2rem;
                resize: none;
                border: 2px solid $primary-color; 
                border-radius: 0.5rem;
            }
            
            input[type=submit] {
                @include button(1.3rem, 17.7rem, 3.7rem, 600);
                background-color: $primary-color;
                color: $light-color;
                margin-top: 2rem;
              }
        }
    }
}


@media screen and (max-width: 1200px) {
    // MAIN RESPONSIVE //
    #contact {
        max-height: 80vh;
        .container {
            width: 100%;
        }
    }

   #contact .container form {
        width: 95%;
   }
}

@media screen and (max-width: 768px) {
    #contact .container form {
        width: 90%;
   }
}

@media screen and (min-width: 1200px) {
    #contact {
        max-height: 100vh;
    }
}
